import React from 'react';
import {WhiteSpace,Flex,WingBlank} from 'antd-mobile';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom'
class Goodlist extends React.Component{
    constructor(props){
        super(props);
    }
    list=(idx)=>{
        const arr=this.props.datalist.datalist; 
        for(let i=idx;i<idx+1;i++){
            return(<Flex.Item>
                <Flex.Item><img src={arr[i].img} style={{width:"100%"}}/></Flex.Item>
                <Flex.Item><p style={{width:"100%"}}>{arr[i].title}</p></Flex.Item>
                <Flex.Item><p style={{color:"red"}}>￥{arr[i].price} <span style={{color:"grey",fontSize:"12px"}}>99人已购买</span></p></Flex.Item>
            </Flex.Item>
            )
        }
    
    }

    render(){
        if(this.props.datalist.length<1){
            return <div></div>
        }else{            
            return (                 
                <WingBlank>
                    <div className="flex-container">
                        <Flex>  
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:0

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(0)}
                            </Flex.Item>
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:1

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(1)}
                            </Flex.Item>                                            
                        </Flex>
                        <Flex>  
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:2

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(2)}
                            </Flex.Item>
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:3

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(3)}
                            </Flex.Item>                                           
                        </Flex>
                        <Flex>  
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:4

                                    }),
                                    this.props.history.push('/list1')
                                )
                               
                                }}>
                                {this.list(4)}
                            </Flex.Item>
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:5

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(5)}
                            </Flex.Item>
                                            
                        </Flex>
                        <Flex>  
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:6

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(6)}
                            </Flex.Item>
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:7

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(7)}
                            </Flex.Item>
                                            
                        </Flex>
                        <Flex>  
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:8

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(8)}
                            </Flex.Item>
                            <Flex.Item onClick={()=>{
                                return (
                                    this.props.dispatch({
                                        type:'ADD_INDEX',
                                        index:9

                                    }),
                                    this.props.history.push('/list1')
                                )
                            }}>
                                {this.list(9)}
                            </Flex.Item>                                           
                        </Flex>
                    </div>
                    <WhiteSpace size="lg"/>
                   <WhiteSpace size="lg"/>
                   <WhiteSpace size="lg"/>
                </WingBlank>
            )    
        }                   
    }
}
const getdata=(state)=>{
    return {datalist:state.datalist,dataindex:state.dataIndex}
}
export default connect(getdata)(withRouter(Goodlist));
