import React from 'react';
import {TabBar} from 'antd-mobile';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
  constructor(props) {
    super(props);
      this.state = {
        pathname:'/'
      };
  }
  render() {
    const {pathname} = this.props.location;
      return (
        <div style={{
          position: 'fixed',
          width: '100%',
          bottom: 0
        }}>
          <TabBar
            unselectedTintColor="#949494"
            tintColor="grey"
            barTintColor="white"
          >
            <TabBar.Item
                title="首页"
                key="home"
                icon={
                  <i className='iconfont icon-shouye'></i>                           
                }
                selectedIcon={
                  <i className='iconfont icon-xuanzhongshangcheng'></i>                           
                }
                selected={ pathname === '/home' }
                onPress={ () => {
                    this.props.history.push('/home');
                } }
                data-seed="logId"
              >
              </TabBar.Item>
              <TabBar.Item
                  icon={
                    <i className='iconfont icon-shoucang1'></i>                           
                  }
                  selectedIcon={
                    <i className='iconfont icon-shoucang1-copy'></i>                           
                  }
                  title="收藏"
                  key="save"
                  selected={ pathname === '/save' }
                  onPress={ () => {
                    // this.props.history.push('/save')
                    this.props.username
                    ? this.props.history.push('/save')
                    :  this.props.history.push('/login')
                  } }
                  data-seed="logId1"
                >
                </TabBar.Item>
              <TabBar.Item
                  icon={
                    <i className='iconfont icon-order'></i>
                  }
                  selectedIcon={
                    <i className='iconfont icon-dingdan'></i>  
                  }
                  title="订单列表"
                  key="Friend"
                  selected={ pathname==='/list'}
                  onPress={ () => {
                    this.props.history.push('/list')
                  } }
                >
                        
			          </TabBar.Item>
              <TabBar.Item
                  icon={<i className='iconfont icon-wode1'></i> }
                  selectedIcon={<i className='iconfont icon-wode'></i>}
                  title="我的淘宝"
                  key="my"
                  selected={pathname==='/my'}
                  onPress={ () => {
                    this.props.history.push('/my');
                  } }
                >                        
			        </TabBar.Item>
            </TabBar>
          </div>
        );
    }
}
const getlogin=(state)=>{
  return {
    username:state.userinfo.username
  }
}
export default connect(getlogin)(withRouter(Mytab));






