import React from 'react';
import {WhiteSpace, SearchBar} from 'antd-mobile';
import Cars from './Cars';
import Mytab from './Mytab';
import Goodlist from './Goodlist';
import {connect} from 'react-redux';
class Home extends React.Component{
    constructor(props){
        super(props);
    }
    componentDidMount(){
        let url='https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url)
        .then(res=>res.json())
        .then(res=>{
            this.props.dispatch({
                type:'DATALIST',
                datalist:res.data
            })
        })   


    }
    
    render(){
        return <div>
        <Mytab />
        {/* 搜索框 */}
            <SearchBar placeholder="寻找宝贝店铺" maxLength={5} 
        onFocus={()=>{this.props.history.push("/statusfocus")}}
        />
        
        {/* 轮播图 */}
        <Cars />
       {/* 商品列表 */}
       <WhiteSpace size='lg'/>
       
       <Goodlist/>

    </div>
        
    }
    
   
}

export default connect()(Home);