import { Flex,Icon } from 'antd-mobile';
import React from 'react';
import {connect} from 'react-redux';
class Save extends React.Component {
    constructor(props){
        super(props);
    }
    list=(idx)=>{
        const arr=this.props.datalist.datalist;
        return(<Flex.Item>
                <Flex.Item><img src={arr[idx].img} style={{width:"100%"}}/></Flex.Item>
                <Flex.Item style={{width:"50%"}}>
                    <p style={{width:"100%"}}>商品名称：{arr[idx].title}</p>
                    <div>
                        商品价格：￥{arr[idx].price}  
                        <button onClick={()=>{
                            this.props.dispatch({
                                type:'DEL_TODO',
                                index:idx
                            })
                        }} style={{top:'-20px',float:'right',right:'-20px'}}>删除</button>
                    </div>
                </Flex.Item>               
            </Flex.Item>)
        
    }
    render(){
        if(this.props.shoucang){
            return (            
                <div>                   
                    <Icon type="left" size="lg" style={{position:"fixed"}} onClick={()=>{this.props.history.push('/home')}}/> 
                    <div>
                        {
                            this.props.shoucang.map((item,i)=>{
                                return(<Flex style={{height:'450px',bottom:'20px'}}>
                                    {this.list(item)}
                                </Flex>)
                                        
                            })
                        }   
                    </div>                   
                                                
                </div>)
            
        }else{
            return <div>
                <Icon type="left" size="lg" style={{position:"fixed"}} onClick={()=>{this.props.history.push('/home')}}/>
                <Flex style={{height:'100px',width:'100%',margin:'0 auto'}}>暂时没有收藏商品</Flex>                 
            </div>
        }
        
    }
    
}
const mapStateToProps=(state)=>{
    return {
        username:state.userinfo.username,
        datalist:state.datalist,
        dataindex:state.dataIndex,
        shoucang:state.shoucang
    }
}
export default connect(mapStateToProps)(Save);