const shoucang=(state='',action)=>{
    switch(action.type){
        case 'ADD_TODO':
            return [...state,action.index];
        case 'DEL_TODO':
            let arr=[...state];
            let index=0;
            for (var i=0;i<arr.length;i++){
                if(arr[i]===action.index){
                    index=i
                }
            }
            arr.splice(index,1);
            return arr;
        default:
            return state;
    }
}
export default shoucang;