import React, { Component } from 'react';
import {connect} from 'react-redux';
import { Icon, Flex,TabBar, Button} from 'antd-mobile';
class List1 extends Component{
    constructor(props){
        super(props);
        this.state = {
            selectedTab: 'blueTab',
            hidden:false
        };
    }
    list=(idx)=>{
        const arr=this.props.datalist.datalist;
        return(<Flex.Item>
            <Flex.Item><img src={arr[idx].img} style={{width:"100%"}}/></Flex.Item>
            <Flex.Item><p style={{width:"100%"}}>{arr[idx].title}</p></Flex.Item>
            <Flex.Item><p style={{color:"red"}}>￥{arr[idx].price} <span style={{color:"grey",fontSize:"12px"}}>99人已购买</span></p></Flex.Item>
        </Flex.Item>)
    }
    render(){   
        return <div>
            <Icon type="left" size="lg" style={{position:"absolute"}} onClick={()=>{this.props.history.push('/home')}}/>
            <Flex style={{top:"0px"}}>
                {this.list(this.props.dataindex)}
            </Flex>
            <div style={{height:"165px"}}></div>
            
            <Flex style={{height:"100%",bottom:"0px"}}>
                <Flex.Item>
                    <TabBar
                        unselectedTintColor="#949494"
                        tintColor="grey"
                        barTintColor="white"
                        tabBarPosition="bottom"
                    >
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-dianpu1'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-dianpu'></i>                           
                            }
                            title="店铺"
                            key="store"
                            selected={this.state.selectedTab === 'blueTab'}
                            onPress={() => {
                                this.setState({
                                    selectedTab: 'blueTab',
                                });
                            }}
                            data-seed="logId"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-kefu1'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-kefu2'></i>                           
                            }
                            title="客服"
                            key="cs"
                            selected={this.state.selectedTab === 'redTab'}
                            onPress={() => {
                                this.setState({
                                    selectedTab: 'redTab',
                                });
                            }}
                            data-seed="logId1"
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-star'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-shoucang'></i>  
                            }
                            title="收藏"
                            key="save"                        
                            selected={this.state.selectedTab === 'greyTab'}
                            onPress={() => {                                
                                this.setState({
                                    selectedTab: 'greyTab',
                                });
                                this.props.dispatch({
                                    type:'ADD_TODO',
                                    index:this.props.dataindex
                                });
                                return (
                                    this.props.username
                                    ?  console.log(this.props.username)
                                    :  this.props.history.push('/login')
                                )
                            }}
                        >                        
			            </TabBar.Item>
                    </TabBar>
                </Flex.Item>
                <Flex.Item style={{margin:0}}>
                    <Button style={{borderRadius:0,backgroundColor:"orange"}}type="warning">加入购物车</Button>
                </Flex.Item>
                <Flex.Item style={{margin:0}}>
                    <Button style={{borderRadius:0,backgroundColor:"red"}} type="warning">立即购买</Button>
                </Flex.Item>
            </Flex>
            
            
        </div>
    }
}
const mapStateToProps=(state)=>{
    return  {
        username:state.userinfo.username,
        datalist:state.datalist,
        dataindex:state.dataIndex,
        shoucang:state.shoucang
    }
}
export default connect(mapStateToProps)(List1);