import React,{Component}from 'react';
import {SearchBar} from 'antd-mobile';

class Statusfocus extends Component {
    constructor(props){
        super(props);
        this.state={
            value:'宝贝'
        }
    }
    onChange= (value) => {
        this.setState({ value });
      };
    render() {
      return (<div>
        <SearchBar
          value={this.state.value}
          placeholder=""
          onCancel={()=>{
              this.props.history.push('/home');
          }}
          onChange={this.onChange}
        />
      </div>);
    }
  }
export default Statusfocus;